import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { A2Edatetimepicker } from '../../directives/datepicker.module';
import { FormsModule } from '@angular/forms';
import { UtilsModule } from '../../utils/utils.module';
import { GeneralConsultaService } from './services/general.consulta.service';
import { TiposService } from './utils/tipos.service';
import { CatalogoDocumentoIdentidadService } from './utils/catalogo-documento-identidad.service';
import {ConsultaModule} from './consulta/consulta.module';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    UtilsModule,
    A2Edatetimepicker,
    ConsultaModule
  ],
  providers: [
    GeneralConsultaService,
    TiposService,
    CatalogoDocumentoIdentidadService
  ]
})
export class GeneralModule { }



